.task {
  display: flex;
  line-height: 40px;
  font-size: 15px;
  background-color: white;
  height: 35px;
  margin-bottom: 10px;
  border-radius: 4px;
  padding: 3px;

  &:first-of-type {
    margin-top: 10px;
  }

  .input {
    display: flex;
    line-height: 40px;
    font-size: 15px;
    font-family: Papyrus, fantasy;
    outline: none;
    width: 245px;
    min-height: 35px;
    background-color: transparent;
    border-width: 0;
  }
}
